body {
  margin: 0;
  padding: 0;
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  background-color: rgb(220, 220, 220);
}

.App {
  text-align: center;
  min-height: 100vh;
  width: 100vw;
  color: rgb(50, 50, 50);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  font-family: Helvetica, Arial, sans-serif;
}

p {
  font-size: 10px;
}

.ReactModal__Body--open {
  overflow-y: hidden;
}

.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: 75px;
  stroke-miterlimit: 10;
}

.App-header {
  min-height: 20vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
/* 
.App-link {
  color: #61dafb;
} */

.data-visuals {
  /* padding-top: 20px; */
}

.locationName {
  margin: 20px 0 20px 0;
  font-weight: 900;
  font-size: 18px;
}

#search-results {
  background-color: #fff7fb;
  width: 92%;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  margin: 0px 0 5px 0;
  padding: 0px 0 0px 0;
  position: absolute;
  z-index: 3;
}

.result {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 5px 0px 5px 0px;

  height: 40px;
  color: black;
}

.result:hover {
  background-color: hsla(0, 0%, 0%, 0.2);
  cursor: pointer;
}

.city {
  font-size: 18px;
  font-weight: 600;
  margin-right: 3px;
}

.state {
  margin-right: 3px;
}

#seek {
  /* display: flex;
  flex-direction: column;
  align-items: center; */
  position: relative;
}

input {
  width: 50vmin;
  height: 40px;
  border: 3px solid #3690c0;
  padding: 5px;
  border-radius: 10px;
  outline: none;
  background-color: rgba(0, 0, 0, 0);
  color: rgb(50, 50, 50);
  font-size: 20px;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.genre-group:hover {
}

.data-bar {
  fill: rgba(167, 167, 167, 0.8);
  /* stroke-alignment: outside; */
}
.empty-bar {
  fill: rgba(167, 167, 167, 0.3);
}
/* 
.data-bar:hover {
  pointer-events: bounding-box;
  fill: coral;
} */

.genre-label {
  font-size: 11px;
  fill: rgb(50, 50, 50);
  text-anchor: end;
}

svg {
  font-family: Helvetica, Arial, sans-serif;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

a {
  color: inherit;
  text-decoration: inherit;
  margin-bottom: 20px;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: rgba(15, 15, 15, 0.75);
  z-index: 10;
}

.modal-content {
  outline: none;
  position: absolute;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: 500px;
  background-color: rgb(209, 209, 209);
  border-radius: 1%;
  padding: 0px 20px 20px 20px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
}

.close-btn {
  fill: black;
  position: absolute;
  width: 20px;
  cursor: pointer;
  top: 0;
  right: 0;
  margin: 15px 15px 0px 0px;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.close-btn:hover {
  fill: #a6bddb;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.modal-container {
  /* margin: 20px 20px 20px 20px; */
  display: grid;
  grid-template-rows: 80px auto;
  height: inherit;
  width: 80vw;
  max-width: 500px;
}

.modal-header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  grid-row: 1;
  height: 80px;
  margin-left: 10px;
  position: fixed;
}

.genre-name {
  font-weight: 900;
  font-size: 24px;
  display: inline;
}

.city-name {
  font-weight: 900;
  font-size: 18px;
  display: inline;
  color: grey;
}

.date-range {
  font-size: 14px;
}

.event-cont {
  overflow-y: scroll;
  grid-row: 2;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: auto;
}

.event-box {
  padding: 10px 10px 10px 10px;
  background-color: white;
  border-radius: 3px;
  cursor: pointer;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.event-box:hover {
  background-color: #a6bddb;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.event-header {
  display: -webkit-flex;
  display: flex;
  font-size: 12px;
  margin-bottom: 8px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
/* 
.event-date {
} */

.event-genres {
  font-style: italic;
  color: grey;
}

.event-name {
  font-weight: 800;
  font-size: 16px;
}

.ReactModal__Overlay {
  opacity: 0;
  -webkit-transition: opacity 200ms ease-in-out;
  transition: opacity 200ms ease-in-out;
}

.ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModal__Overlay--before-close {
  opacity: 0;
}

